<template>
  <div class="container">
    <!--  -->
    <van-nav-bar fixed left-arrow @click-left="$router.back()" title=""></van-nav-bar>

    <!--  -->
    <div class="chat-list">
      <!-- div -->
      <div v-for="(item, index) in chatList" :key="index">
        <!--  -->
        <div class="chat-item left" v-if="item.name !== 'user'">
          <!-- <van-image fit="cover" round src="" /> -->
          <img src="../../assets/kefu.jpg" alt="" class="van-image" />
          <div class="chat-pao">{{ item.message }}</div>
        </div>

        <!--  -->
        <div class="chat-item right" v-else>
          <div class="chat-pao">{{ item.message }}</div>
          <van-image fit="cover" round :src="$store.state.userPhoto" />
        </div>
      </div>
    </div>

    <!--  -->
    <div class="reply-container van-hairline--top">
      <van-field placeholder="..." v-model="userInputMessage">
        <template #button>
          <span style="font-size: 12px; color: #999" @click="sendMessageFn"></span>
        </template>
      </van-field>
    </div>
  </div>
</template>

<script>
// 1 iosocket.io-client
import { io } from 'socket.io-client'
export default {
  data() {
    return {
      // 
      userInputMessage: '',
      // 
      chatList: [
        {
          name: 'robot',
          message: ''
        }
      ],
      // 2 socket
      socket: null
    }
  },
  // 3 websocket
  created() {
    this.socket = io('http://geek.itheima.net', {
      query: {
        token: localStorage.getItem('token')
      },
      transports: ['websocket']
    })
    // 4 
    this.socket.on('connect', () => {
      console.log('')
    })
    // 5 
    this.socket.on('message', (obj) => {
      this.chatList.push({
        name: 'robot',
        // obj.message
        message: obj.msg
      })
    })

    // 
    this.$nextTick(() => {
      const lastChatDiv = document.querySelector('.chat-list>div:last-child')
      // console.log(lastChatDiv)
      // Domdiv,vuenextTick()
      lastChatDiv.scrollIntoView({
        behavior: 'smooth'
      })
    })
  },
  methods: {
    // 6 
    sendMessageFn() {
      // 
      if (this.userInputMessage.trim().length === 0) return
      this.socket.emit('message', {
        msg: this.userInputMessage,
        timestamp: new Date().getTime()
      })
      // 
      this.chatList.push({
        name: 'user',
        message: this.userInputMessage
      })
      // 
      this.$nextTick(() => {
        const lastChatDiv = document.querySelector('.chat-list>div:last-child')
        // console.log(lastChatDiv)
        // Domdiv,vuenextTick()
        lastChatDiv.scrollIntoView({
          behavior: 'smooth'
        })
      })
      // 
      this.userInputMessage = ''
    }
  },
  // socketsocket
  destroyed() {
    this.socket.close()
    this.socket = null
  }
}
</script>

<style lang="less" scoped>
.container {
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  box-sizing: border-box;
  background: #fafafa;
  padding: 46px 0 50px 0;
  .chat-list {
    height: 100%;
    overflow-y: scroll;
    .chat-item {
      padding: 10px;
      .van-image {
        vertical-align: top;
        width: 40px;
        height: 40px;
      }
      .chat-pao {
        vertical-align: top;
        display: inline-block;
        min-width: 40px;
        max-width: 70%;
        min-height: 40px;
        line-height: 38px;
        border: 0.5px solid #7bc6ff;
        border-radius: 4px;
        position: relative;
        padding: 0 10px;
        background-color: #7bc6ff;
        word-break: break-all;
        font-size: 14px;
        color: #333;
        &::before {
          content: '';
          width: 10px;
          height: 10px;
          position: absolute;
          top: 12px;
          border-top: 0.5px solid #7bc6ff;
          border-right: 0.5px solid #7bc6ff;
          background: #7bc6ff;
        }
      }
    }
  }
}
.chat-item.right {
  text-align: right;
  .chat-pao {
    margin-left: 0;
    margin-right: 15px;
    // border: 0.5px solid #e0e0e0;
    // background-color: #e0e0e0;
    // border-top: 0.5px solid #e0e0e0;
    // border-right: 0.5px solid #e0e0e0;
    // background: #e0e0e0;
    &::before {
      right: -6px;
      transform: rotate(45deg);
    }
  }
}
.chat-item.left {
  text-align: left;
  .chat-pao {
    margin-left: 15px;
    margin-right: 0;
    // border: 0.5px solid #7bc6ff;
    // background-color: #7bc6ff;
    // border-top: 0.5px solid #7bc6ff;
    // border-right: 0.5px solid #7bc6ff;
    // background: #7bc6ff;

    &::before {
      left: -5px;
      transform: rotate(-135deg);
    }
  }
}
.reply-container {
  position: fixed;
  left: 0;
  bottom: 0;
  height: 44px;
  width: 100%;
  background: #f5f5f5;
  z-index: 9999;
}
</style>
